<script>
let BITBOX = require('bitbox-sdk').BITBOX
let bitbox = new BITBOX()

let langs = [
  'english',
  'chinese_simplified',
  'chinese_traditional',
  'korean',
  'japanese',
  'french',
  'italian',
  'spanish'
]

var seed1;

var seed1, seed2, wallet1, wallet2, register;

export default {
  name: 'BitBox',
  data() {
    return {
      register: register,
      seed1: seed1,
      seed2: seed2,
      wallet1: wallet1,
      wallet2: wallet2,
      getDefaultAddress: function(wallet) {
        return bitbox.HDNode.toCashAddress(wallet);
      }
    }
  },
  methods: {
    isNotEmpty: function(string) {
      var empty = typeof string === 'undefined' || string === '';
      return !empty;
    },
    generateMnemonic: function (seed) {
      this[seed] = bitbox.Mnemonic.generate(256, bitbox.Mnemonic.wordLists()['english']);
    },
    generateWallet: function (seed, wallet) {
      let rootSeed = bitbox.Mnemonic.toSeed(this[seed]);
      let masterHDNode = bitbox.HDNode.fromSeed(rootSeed, 'mainnet');
      this[wallet] = masterHDNode;
      console.log(this)
      console.log(this[wallet])
    }
  }
}
</script>

